@import url(https://fonts.cdnfonts.com/css/lcd);
iframe{display:none}html{font-family:"LCD", sans-serif;font-size:24px}body{display:flex;justify-content:center;align-items:center;height:100vh;margin:0;background-image:url(https://www.itl.cat/pngfile/big/253-2538296_wallpaper-black-hole-eclipse-stars-singularity-black-hole.jpg);background-size:cover;background-position:center;background-repeat:no-repeat}.App{display:flex;justify-content:center;align-items:center;flex-direction:column;width:400px;height:400px;padding:10px;text-align:center;color:#04fefc;background-color:black;border-radius:50%}.Clock__time{display:flex;justify-content:center;align-items:center;position:relative;z-index:1;width:-webkit-max-content;width:max-content;height:100px;margin:20px auto 0;padding:0 10px;font-size:50px;line-height:200px;font-weight:600;border-radius:20px;overflow:hidden}.Clock__time::before{content:"";position:absolute;z-index:-1;width:120%;height:50px;background-color:#0006a7;-webkit-animation:animate 4s linear infinite;animation:animate 4s linear infinite}.Clock__time::after{content:"";position:absolute;z-index:-1;background:black;inset:4px;border-radius:15px}@-webkit-keyframes animate{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes animate{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

